@use '../base';

.form-container {
  position: absolute;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 1rem;
  left: 1rem;
  z-index: 10;
  background-color: white;
  padding: 2em;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

  &__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__waypoint-button {
    margin: 1em 0;
    background-color: base.$accent-color;
    border-radius: 50%;
    border: none;
    width: 1.5em;
    height: 1.5em;
    font-size: 18px;

    &:hover {
      border: 1px solid black;
    }

    &:active {
      background: none;
    }
    
    &--hidden {
      display: none;
    }
  }
  
  &__input {
    display:block;
    height: 30px;
    width: 200px;
    margin: 0.2em auto;
    border: none;
    border-bottom:1px solid #84909f;

    &::placeholder {
      -webkit-transform:translateY(0px);
      transform:translateY(0px);
      -webkit-transition:.5s;
      transition:.5s;
    }

    &:hover,
    &:focus,
    &.active:focus {
      color: base.$button-color;
      outline:none;
      border-bottom:1px solid #84909f;
    }

    &--hidden {
      display: none;
    }
  }

  &__button {
    border: none;
    color: white;
    background-color: base.$button-color;
    border-radius: 8px;
    font-weight: bold;
    font-size: 1em;
    padding: .5em 1em;
    margin: 1em 0;
    letter-spacing: 1px;
    align-items: center;

    &:hover {
    opacity: 0.8;
    }
  }
}

.marker-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 250px;
  margin: 0.4em 0;

  &__name {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size:small;
    width: 170px;
  }

  &__btn {
    // margin-top: 1em;
    margin-left: .5em;
    background: none;
    border: none;
    width: 1.5em;
    height: 1.5em;
    font-size: 18px;

    &:hover {
     opacity: 0.6;
    }

    &:active {
      background: none;
    }
    
    &--hidden {
      display: none;
    }
  }
}